  public message: string = null;
  public fakeChat: Array<any> = [];
  public messages: Array<any> = [];
  public student: Student = null;
  public chat: any = null;
  private messagesSubscriber: any = null;
  private chatRoomSubscriber: any = null;
  @ViewChild(IonContent) private content: IonContent;

  constructor(
    private dataService: DataService
  ) { }

  ngOnInit() {
    this.student = JSON.parse(localStorage.getItem('student'));
    this.chatRoomSubscriber = this.dataService.getById(FirebaseCollections.CHAT_ROOM, this.student.chat_id || '1234').subscribe(resp => {
      this.chat = resp;
    }, err => {
      console.error('Error while getting chat::::::::::::\n', err);
    });
    this.messagesSubscriber = this.dataService.getMessages(this.student.chat_id || '1234').subscribe(resp => {
      this.messages = resp;
      this.updateScroll();
    }, err => {
      console.error('Error while getting messages::::::::::::\n', err);
    });
    this.fakeChat = this.getFakeChat();
  }

  sendMessage() {
    if (this.message.trim().length <= 0) {
      this.message = '';
      return;
    } else {
      const chatRoom: any = {
        last_msg: this.message,
        unread_messages: this.chat?.is_opened ? 0 : this.chat.unread_messages + 1,
        id: this.student.chat_id || 'jjjjjj'
      };
      this.dataService.update(FirebaseCollections.CHAT_ROOM, chatRoom).then((res: any) => {
        const message: any = {
          message: this.message,
          student_id: this.student.id,
          student_name: this.student.first_name + ' ' + this.student.last_name,
          chat_id: this.student.chat_id,
          is_student: true
        };
        this.dataService.add(FirebaseCollections.MESSAGES, message).then((resp: any) => {
          this.message = '';
          this.updateScroll();
        }, err => {
          console.error('Error while adding message:::::::::::\n', err);
        });
      }, err => {
        console.error('Error while updating chat room:::::::::::\n', { ...err });
        if (err.code === 'not-found' || err.code === 'invalid-argument') {
          const chatRoom: any = {
            student_id: this.student.id,
            student_name: this.student.first_name + ' ' + this.student.last_name,
            last_msg: this.message,
            unread_messages: this.chat?.is_opened ? 0 : this.chat.unread_messages + 1,
            updated_at: serverTimestamp()
          };
          this.dataService.add(FirebaseCollections.CHAT_ROOM, chatRoom).then((resp: any) => {
            // console.log(resp);
            this.student.chat_id = resp.id;
            this.dataService.update(FirebaseCollections.STUDENTS, { chat_id: resp.id, id: this.student.id }).then((response: any) => {
              localStorage.setItem('student', JSON.stringify({ ...this.student }));
            }, (error: any) => {
              console.log('Error while updating student:::::::::\n', error);
            });
            const message: any = {
              message: this.message,
              student_id: this.student.id,
              student_name: this.student.first_name + ' ' + this.student.last_name,
              chat_id: this.student.chat_id,
              is_student: true
            };
            this.dataService.add(FirebaseCollections.MESSAGES, message).then((res: any) => {
              this.message = '';
              setTimeout(() => {
                this.updateScroll();
              }, 500);
            }, error => {
              console.error('Error while adding notification:::::::::::\n', error);
            });
          }, (error: any) => {
            console.error('Error while adding message:::::::::::\n', error);
          });
        }
      });
    }
  }

  updateScroll() {
    setTimeout(() => {
      this.content.scrollToBottom(1000);
    }, 500);
  }

  ngOnDestroy() {
    this.chatRoomSubscriber.unsubscribe();
    this.messagesSubscriber.unsubscribe();
  }

